<template>
  <SignupForm :isDarkTheme="isDarkTheme" />
</template>

<script>
import SignupForm from './components/SignupForm.vue'

export default {
  name: 'App',
  components: { SignupForm },
  data(){
    return {
      isDarkTheme: true
    }
  },
  mounted(){
    if(this.isDarkTheme)
    {
      document.body.setAttribute("style", "background-color: rgb(15, 13, 13);")
    }
  }
}
</script>

