<template>
    Email: {{email}} <br>
    Password: {{password}} <br>
    Role: {{role}} <br>
    Skills: {{skills}} <br>
    isTermsAccepted: {{isTermsAccepted}} <br>
    passwordErrors: {{passwordErrors}} <br>
  <div class="effectContainer">
    <div class="dot" tabindex="0"></div>
    <div class="dot" tabindex="0"></div>
    <div class="dot" tabindex="0"></div>
    <div class="dot" tabindex="0"></div>
  </div>
  <form :class="{ dark: isDarkTheme }" @submit.prevent="handleSubmit">
    <label>*Email:</label>
    <input type="email" v-model="email" required>
    <!-- <input type="email" @input="event => email = event.target.value"  required> -->

    <label>*Password:(Minimum 7 characters)</label>
    <input type="password" v-model="password" required>
    <div class="passError" v-if="passwordErrors.passError">{{passwordErrors.passError}}</div>

    <label>Role:</label>
    <select v-model="role">
        <option value="developer">Web Developer</option>
        <option value="designer">Web Designer</option>
    </select>

    <label>Skills: (Use , key to add smth bro)</label>
    <input type="text" v-model="tempSkill" @keyup="addSkill">
    <div class="skill" v-for="(skill, index) in skills" :key="skill" @click="deleteSkill(index)">
        {{skill}}
    </div>
    <div class="terms">
        <label><input type="checkbox" v-model="isTermsAccepted">*Accept Terms and Conditions:</label>
        <div class="passError" v-if="passwordErrors.conditionError">{{passwordErrors.conditionError}}</div>
    </div>

    <button>create my little pony world</button>
  </form>
</template>

<script>
export default {
    data(){
        return{
            email: '',
            password: '',
            role: 'designer',
            skills: [],
            tempSkill: '',
            isTermsAccepted: false,
            passwordErrors: {
                passError: '',
                conditionError: ''
            }
        }
    },
    props: {
        isDarkTheme: Boolean
    },
    methods:{
        addSkill(e){
            if(e.key === ',' && this.tempSkill)
            {
                this.tempSkill = this.tempSkill.split(',').join('')
                if(this.tempSkill && !this.skills.includes(this.tempSkill.toLowerCase()))
                {
                    this.skills.push(this.tempSkill)
                }
                this.tempSkill = ''
            }
        },
        deleteSkill(i){
            this.skills.splice(i, 1)
        },
        handleSubmit(){
            this.passwordErrors.passError = (this.password.length < 7) ? "bro you should have at least 7 chars" : ''
            this.passwordErrors.conditionError = (!this.isTermsAccepted) ? "seriously? check the box to continue bro" : ''
        }
    }
}
</script>

<style>
    form{
        max-width: 420px;
        background-color: white;
        text-align: left;
        margin: 0 auto;
        padding: 40px;
        border-radius: 10px;
    }
    label{
        display: inline-block;
        color: #999;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 0.6em;
        letter-spacing: 1px;
        margin: 16px 0;
    }
    input, select{
        display: block;
        padding: 10px 6px;
        width: 100%;
        border: none;
        border-bottom: 1px solid #eee;
        color: #555;
        box-sizing: border-box;
    }
    input[type="checkbox"]{
        position: relative;
        display: inline-block;
        width: 16px;
        top: 2px;
    }
    button{
        display: block;
        margin: 0 auto;
        border: none;
        padding: 16px;
        border-radius: 32px;
        background-color: rgb(226, 116, 89);
        color: white;
        letter-spacing: 1px;
        cursor: pointer;
    }
    .passError{
        color: red;
        font-weight: 700;
    }
    .skill{
        display: inline-block;
        background-color: rgb(187, 81, 81);
        color: white;
        margin: 10px 10px 0 0;
        padding: 4px 8px;
        border-radius: 32px;
        user-select: none;
        font-size: 1em;
    }

    form.dark{
        background-color: rgb(88, 88, 88);
    }
    .dark label{
        color: #eee;
    }
    .dark input, .dark select{
        background-color: rgb(88, 88, 88);
        color: white;
    }

    .effectContainer{
        position: absolute;
        width: 100vw;
        height: 100vh;
        top: 0;
        z-index: -1;
        overflow: hidden;
    }
    .dot{
        position: absolute;
        background-color: rgba(74, 155, 151, 0.788);
        width: 64px;
        height: 64px;
        border-radius: 50%;
        transform: scale(1) translateY(115vh);
        transition: 1s;
    }

    .dot:nth-child(2n + 1){
        left: 11vw;
        animation: toTop 10s cubic-bezier(0.775, 0.4, 0.72, 0.5) infinite;
        animation-delay: 2s;
        filter: hue-rotate(20deg);
    }
    .dot:nth-child(2n + 2){
        left: 35vw;
        animation: toTop 8s cubic-bezier(0.775, 0.4, 0.72, 0.5) infinite;
        animation-delay: 15s;
        filter: hue-rotate(40deg);
    }
    .dot:nth-child(2n + 3){
        left: 78vw;
        animation: toTop 14s cubic-bezier(0.775, 0.4, 0.72, 0.5) infinite;
        animation-delay: 20s;
        filter: hue-rotate(60deg);
    }
    .dot:nth-child(2n + 4){
        left: 56vw;
        animation: toTop 9s cubic-bezier(0.775, 0.4, 0.72, 0.5) infinite;
        animation-delay: 30s;
        filter: hue-rotate(80deg);
    }

    .dot::before{
        left: 0;
        position: absolute;
        content: '';
        box-shadow: 0 0 20px rgba(0, 174, 255, 0.753),
                    0 0 40px rgba(0, 174, 255, 0.377),
                    0 0 80px rgba(0, 255, 64, 0.623);
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }

    .dot::after{
        left: 15%;
        top: 13%;
        position: absolute;
        background-color: rgb(255, 255, 255);
        content: '';
        box-shadow: 0 0 20px rgba(0, 174, 255, 0.753),
                    0 0 40px rgba(0, 174, 255, 0.377),
                    0 0 170px rgba(0, 255, 64, 0.623),
                    inset 0 0 10px rgba(0, 174, 255, 0.753);
        width: 70%;
        height: 70%;
        border-radius: 50%;
    }

    .dot:focus{
        box-shadow: 0 0 200px 80px rgba(0, 174, 255, 0.753);
        opacity: 0;
    }

    .dot:focus::before{
        content: 'CATCHED';
        top: -20px;
        left: -100%;
        color: white;
        box-shadow: none;
        font-size: 2em;
        width: 200px;
    }


    @keyframes toTop {
        /* from{
            transform: scale(1) translateY(115vh);
        } */
        to{
            transform: scale(0) translateY(0);
        }
    }
    
    

</style>